<template>
  <div class="supplement-page relative min-w-[1200px]">
    <page-header></page-header>
    <main class="w-[1200px] mx-auto">
      <div class="mt-5 pl-2 text-[#aeb9c0]">首页>在线工具><span class="blue-text">智能选题</span></div>
      <section>
        <div class="text-center mt-6">
          <img src="@/assets/supplement_tool.png" class="w-[120px]" />
        </div>
        <h1 class="text-center text-[44px] mt-4 mb-12">析易-回顾性队列研究补证工具</h1>
      </section>
      <section class="shadow-[0_0_14px_4px_rgba(0,0,0,0.1)] pl-4 pr-8 pt-10 pb-4 mb-12 rounded-lg">
        <el-form label-width="120px" :model="form" :rules="rules" ref="supplementForm">
          <el-form-item label="段落选择" prop="paragraphValue" class="w-full flex justify-center">
            <el-select v-model="form.paragraphValue" placeholder="请选择段落选择" class="w-[600px]" @change="changeParagraphValue">
              <el-option v-for="item in paragraphOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="模块" prop="moduleValue" class="w-full flex justify-center">
            <el-select v-model="form.moduleValue" placeholder="请选择模块" class="w-[600px]" @change="changeModuleValue">
              <el-option v-for="item in moduleOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="修证内容" prop="contentValue" class="w-full flex justify-center">
            <div class="w-[600px]">
              <el-input v-model="form.contentValue" :placeholder="contentPlaceholder" maxlength="100" :disabled="fristIndex == 3"></el-input>
            </div>
          </el-form-item>
          <el-form-item label="上传参考文献" class="w-full pl-4" prop="file">
            <file-upload ref="normalIntroductionRef" desc="为保证学习质量，暂不支持图片图表、音视频等最多可传20个;支持扩展名：.pdf" @getFile="getFile"></file-upload>
          </el-form-item>
        </el-form>
        <div class="text-center pl-4 mt-8 mb-10">
          <el-button type="primary" size="medium" class="w-[200px]" @click="onSumbit">提交</el-button>
        </div>
      </section>
    </main>
    <section class="w-[1200px] mx-auto my-12 px-9 border border-dashed border-[#2b323d] flex items-center justify-between h-[150px]" v-if="file.name">
      <div class="flex items-center">
        <img class="w-[64px]" src="@/assets/word.png" />
        <div class="flex-1 overflow-hidden ml-3">
          <div class="break-all truncate">{{ file.name }}</div>
          <div class="mt-2 text-xs text-[#999]">{{ bytesToSize(file.size) }}</div>
        </div>
      </div>
      <div>
        <el-button class="w-[150px]" @click="onFileView">预览</el-button>
        <el-button type="primary" class="w-[150px]" @click="onDownload">下载文件</el-button>
      </div>
    </section>
    <page-footer></page-footer>
  </div>
</template>

<script>
import PageHeader from "@/views/components/PageHeader.vue"
import PageFooter from "@/views/components/PageFooter.vue"
import FileUpload from "@/views/components/FileUpload.vue"
import { bytesToSize } from "@/utils/validate"

export default {
  components: {
    PageHeader,
    PageFooter,
    FileUpload,
  },
  data() {
    return {
      form: {
        paragraphValue: "",
        moduleValue: "",
        contentValue: "",
        file: "",
      },
      rules: {
        paragraphValue: [{ required: true, message: "请选择段落选择", trigger: ["blur", "change"] }],
        moduleValue: [{ required: true, message: "请选择模块", trigger: ["blur", "change"] }],
        contentValue: [{ required: true, message: "请输入修证内容", trigger: ["blur", "change"] }],
        file: [{ required: true, message: "请上传参考文献", trigger: ["blur", "change"] }],
      },
      contentPlaceholder: "请输入修证内容",
      paragraphOptions: [
        {
          value: "0",
          label: "引言",
          children: [
            {
              value: "0",
              label: "疾病说明",
              placeholder: "例如：息肉",
            },
            {
              value: "1",
              label: "治疗方法",
              placeholder: "例如：中医治疗结直肠息肉方法",
            },
          ],
        },
        {
          value: "1",
          label: "资料与方法",
          children: [
            {
              value: "0",
              label: "指标说明",
              placeholder: "例如：术后 IL-6 水平（pg/mL）的定义及作用",
            },
            {
              value: "1",
              label: "治疗方法操作流程",
              placeholder: "例如：冷切套息肉治疗术治疗中小型结肠息肉的治疗过程和操作",
            },
          ],
        },
        {
          value: "2",
          label: "讨论",
          children: [
            {
              value: "0",
              label: "疾病机理",
              placeholder: "例如：中小型结肠息肉发病机理",
            },
            {
              value: "1",
              label: "治疗原理",
              placeholder: "例如：冷切套息肉治疗术治疗中小型结肠息肉的原理",
            },
            {
              value: "2",
              label: "指标对比解释",
              placeholder: "例如：冷切套息肉治疗术组的术后出血量小于热切套息肉治疗术组，两组呈现显著性差异",
            },
          ],
        },
        {
          value: "3",
          label: "摘要",
          children: [
            {
              value: "0",
              label: "中英文",
            },
            {
              value: "1",
              label: "中文",
            },
            {
              value: "2",
              label: "英文",
            },
          ],
        },
      ],
      moduleOptions: [],
      file: {
        name: "",
        url: "",
        size: "",
      },
      fristIndex: "",
    }
  },
  methods: {
    getFile(value) {
      this.form.file = value
      this.$nextTick(() => {
        this.$refs["supplementForm"].clearValidate("file")
      })
    },
    bytesToSize(bytes) {
      return bytesToSize(bytes)
    },
    onSumbit() {
      this.$refs["supplementForm"].validate((valid) => {
        if (valid) {
          console.log("supplement submit!")
          resolve(this.form)
        } else {
          console.log("error supplement sumbit!")
          reject()
        }
      })
      this.file = {
        name: "心外膜消融术临床实际意义是否大于射频消融术",
        url: "/static/内镜下冷圈套切除术治疗微小型结直肠息肉疗效评估.pdf",
        size: "2344",
      }
    },
    onFileView() {
      window.open(this.file.url)
    },
    getFileExtension(filename) {
      return /[.]/.exec(filename) ? /[^.]+$/.exec(filename)[0] : ""
    },
    onDownload() {
      const url = this.file.url
      const fileExtension = this.getFileExtension(url)
      const name = this.file.name + "." + fileExtension
      this.downloadFileFromApi(url, name)
    },
    async downloadFileFromApi(fileUrl, fileName) {
      try {
        const response = await fetch(fileUrl)
        const blob = await response.blob()
        const url = URL.createObjectURL(blob)

        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", fileName)
        document.body.appendChild(link)
        link.click()

        // 清理工作
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
      } catch (error) {
        console.error("下载失败:", error)
      }
    },
    changeParagraphValue(val) {
      this.fristIndex = val
      this.moduleOptions = this.paragraphOptions[val].children
      this.form.moduleValue = ""
      this.form.contentValue = ""
      this.contentPlaceholder = "请输入修证内容"
      this.$nextTick(() => {
        this.$refs["supplementForm"].clearValidate("moduleValue")
        this.$refs["supplementForm"].clearValidate("contentValue")
      })
    },
    changeModuleValue(val) {
      this.contentPlaceholder = this.paragraphOptions[this.fristIndex].children[val].placeholder || "请输入修证内容"
      this.$nextTick(() => {
        this.$refs["supplementForm"].clearValidate("contentValue")
      })
    },
  },
  destroyed() {},
}
</script>
<style scoped lang="scss">
.supplement-page {
  background: linear-gradient(to bottom, #ebfafb, #feffff), url("~@/assets/bg.png") no-repeat center top;
}
</style>
<style lang="scss">
.supplement-page {
  .el-form--label-top .el-form-item__label {
    padding-bottom: 0;
  }

  .el-upload-dragger {
    padding-bottom: 20px;
  }

  .el-upload,
  .el-upload-dragger {
    width: 100%;
    height: auto;
  }

  .el-card {
    overflow: visible;
  }
  .el-form-item__content {
    margin-left: 0 !important;
  }
}
</style>
